img {
  width: 100%;
  border-radius: 10px;
}

.question-answers {
  display: grid;
  grid-auto-flow: row;
  grid-template-columns: repeat(3, 1fr);
  justify-content: stretch;
  align-items: stretch;
  flex-wrap: wrap;
  // margin: 2.5px;
}

.answer-text {
  width: 15em;
  text-align: center;
  text-transform: none;
}

.answer {
  margin: 2.5px;
  height: 6em;
}

.delayed-appearance {
  opacity: 0;
  transition-duration: 0.3s;
  user-select: none;
}

.delayed-appearance.shown {
  transition-property: opacity;
  transition-delay: 1s;
  opacity: 1;
}

sub {
  vertical-align: baseline;
  position: relative;
  top: 0.3em;
  font-size: 0.7em;
}

